.profile__add-button {
    width: 150px;
    height: 50px;
    background-color: transparent;
    border-radius: 2px;
    cursor: pointer;
    transition: .5s;
    background-image: url(../../../images/add_button_full_svg.svg);
    background-repeat: no-repeat;
    border: none;
    padding: 0;
}

.profile__add-button:hover {
    opacity: .6;
}

@media screen and (max-width: 1000px) {
    .profile__add-button {
        width: 282px;
        margin-top: 36px;
        background-image: url(../../../images/add_button_full_mobile_svg.svg);
    }
}