.popup__button-save {
    background-color: #000;
    border: none;
    border-radius: 2px;
    color: #fff;
    cursor: pointer;
    font-size: 18px;
    font-weight: 400;
    height: 50px;
    line-height: 22px;
    margin: 0 36px 37px 36px;
    padding: 0;
    text-align: center;
    width: 358px;
    transition: 0.5s;
}

.popup__button-save:hover {
    opacity: 0.8;
}


@media screen and (max-width: 550px) {
    .popup__button-save {
        max-width: 238px;
        margin: 0 22px 25px;
        font-size: 14px;
        line-height: 17px;
    }
}