.profile__title {
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    margin: 0;
    color: #FFFFFF;
    align-self: end;
    max-width: 350px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 720px) {
    .profile__title {
        font-size: 27px;
        line-height: 33px;
        max-width: 280px;
        text-align: center;
        padding-top: 5px;
    }
}