.popup__input {
    border: 1px solid transparent;
    border-bottom-color: rgba(0,0,0,.2);
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    margin: 0 36px 27px;
    overflow: hidden;
    padding: 0 0 10px 0;
    text-overflow: ellipsis;
    vertical-align: top;
    white-space: nowrap;
    width: 358px;
}

@media screen and (max-width: 550px) {
    .popup__input {
        max-width: 238px;
        margin: 0 22px 27px;
    }
    .popup__input:nth-child(2) {
        margin-bottom: 44px;
    }
    
}